import React from 'react'
import './Profile.css'
const proimg2 = "data:image/jpeg;base64,";
const proimg = "data:image/jpeg;base64,";
class Profile extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            rot: false,
            type: false,
            address: false,
            name: 0,
            addressindex: 0

        }
    }
    componentDidMount() {
        this.rotate();
    }
    rotate = () => {
        var t = document.getElementById("proimg");
        this.setState({ rot: !this.state.rot });
        this.setState({ type: !this.state.type, name: 0, addressindex: 0 });
        if (this.state.rot) {
            t.className = "rotant";
            this.setState({ address: false });
        }
        else {
            t.className = "rotclk";
        }
    }
    showmore = () => {
        this.setState({ address: true });
    }
    typing = (type) => {
        if (type === "name" && this.state.name == 0) {
            this.typeName();
        }
    }

    typeName = () => {
        const txt = "Adarsh S";
        if (this.state.name < txt.length) {
            if (document.getElementById("name")) {
                document.getElementById("name").innerHTML += txt.charAt(this.state.name);
                this.setState({ name: this.state.name + 1 });
            }
            setTimeout(this.typeName, 200);
        }
        else {
            this.setState({ address: true });
        }
    }
    render() {
        return (
            <div className="prohome">
                <div id="proimg">
                    <img src={this.props.mode ? proimg2 : proimg} alt="" onClick={this.rotate}>
                    </img>
                </div>
                {this.state.type ?
                    <div className="text">
                        {this.typing("name")}
                        <span id="name"></span>
                        {this.state.address ?
                            <div style={{ marginTop: "1rem" }}>
                                Vasthupam,
                                Mangaram,<br />
                                 Konni PO
                                 Pathanamthitta, <br />
                                  Kerala, India
                                   Pin:689691
                                <div style={{ marginTop: "1rem" }}>
                                    <i>Contact me:</i> <br />
                                    &ensp;<a href="tel: +919207676309">+91 9207676309</a> <br />
                                    &ensp;<a href="tel: +919074036526">+91 9074036526</a> <br />
                                    &ensp;<a href="mailto:vasthoopham.ky@gmail.com">vasthoopham.ky@gmail.com</a>
                                </div>
                                <div style={{ marginTop: "1rem" }}>
                                <a href="../../Assets/Images/Resume.pdf" className="resume-download" download="Resume.pdf">Download my Resume</a>
                                </div>
                            </div> : null}
                    </div> : null}
            </div>
        );
    }
}
export default Profile;